import React from "react";
import { Kids } from "../component/PortfolioPhotos/Kids";
import { Baby } from "../component/PortfolioPhotos/Baby";

export class Portfolio extends React.Component {
  state = {
    isKids: true,
    isBaby: false,
    isPregnancy: false,
    isPortrait: false,
    isFamily: false,
    isWedding: false
  };

  onClickKids = () => {
    this.setState({
      isKids: true,
      isBaby: false,
      isPregnancy: false,
      isPortrait: false,
      isFamily: false,
      isWedding: false
    });
  };

  onClickBaby = () => {
    this.setState({
      isKids: false,
      isBaby: true,
      isPregnancy: false,
      isPortrait: false,
      isFamily: false,
      isWedding: false
    });
  };
  render() {
    return (
      <div className="container">
        <div className="row mb-5">
          <button
            type="button"
            className="btn btn-outline-info mx-auto"
            onClick={this.onClickBaby}
          >
            Sesje noworodkowe
          </button>

          <button
            type="button"
            className="btn btn-outline-info mx-auto"
            onClick={this.onClickKids}
          >
            Sesje dziecięce
          </button>

          <button type="button" className="btn btn-outline-info mx-auto">
            Sesje ciązowe
          </button>

          <button type="button" className="btn btn-outline-info mx-auto">
            Sesje portretowe
          </button>

          <button type="button" className="btn btn-outline-info mx-auto">
            Sesje rodzinne
          </button>

          <button type="button" className="btn btn-outline-info mx-auto">
            Sesje slubne
          </button>
        </div>
        <div className="row">
          {this.state.isKids && <Kids />}
          {this.state.isBaby && <Baby />}
        </div>
      </div>
    );
  }
}
